:host {
  flex: 1;
  display: flex;
}

.container {
  flex: 1;
  display: flex;
}

::ng-deep .sketch-container .mat-drawer-backdrop.mat-drawer-shown {
  visibility: visible !important;
}

::ng-deep .mat-drawer {
  border-radius: 0px;
}

.sidenav-container {
  background-color: #faf9fd;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.sidenav-header {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
}

.sidenav-body {
  padding: 10px 20px;
  overflow: hidden;
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.sidenav-item {
  padding: 5px 10px;
  cursor: pointer;
}

.selected {
  background-color: rgba(0, 123, 255, 0.5);
  // color: white;
  // filter: brightness(0.);
}

.menu-text {
  font-size: 20px;
}

.sketch-container {
  width: 100%;
  height: 100%;
  background-color: #faf9fd;
  display: flex;
  flex-direction: column;
}

.container-header {
  // border: 1px solid lightgray;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0px 10px;
}

.container-body {
  flex: 1;
  box-shadow: inset;
  // padding: 10px 30px 10px 30px;
  background-color: #edf0f4;
  border: 1px solid lightgray;
  // flex: 5;
  display: flex;
  overflow: hidden;
}

.app-page {
  width: 100%;
  height: 100%;
  background-color: #faf9fd;
  overflow-y: auto;
}

.create-handler-container {
  position: absolute;
  bottom: 0;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.spacer {
  flex: 1;
}

::ng-deep .widget-dialog {
  border-radius: 5px;
  overflow: hidden;
}

::ng-deep .widget-dialog .mat-dialog-container {
  border-radius: 5px;
  overflow: hidden;
}
